.nav{
  width: 100%;
  height:1rem;
  padding:0 1rem;
  position: fixed;
  background-color: $color-primary;
  display: flex;
  justify-content: center;
  z-index:1 ;

  @include respond(big-desktop){
    padding:0 5rem;
    height: 5vh;
  }

  @include respond(tab-land){
    height: 11vh;
  }

  @include respond(phone){
    height: 8vh;
  }

  &-bar{
    width: 100%;
    display: flex;
    position: relative;
    align-items: center;

    &--logo{
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1rem;
      font-weight: 500;


      img{
        width:1rem;
        margin-right: .1rem;

        @include respond(big-desktop){
          width: 5rem;
        }

        @include respond(tab-land){
          width: 7rem;
        }

        @include respond(phone){
          width: 7rem;
        }
      }

      span{
        letter-spacing: .1rem;
        font-size: .3rem;
        font-weight: 400;
        text-transform: uppercase;
        display: flex;
        justify-content: flex-start;

        @include respond(big-desktop){
          font-size:1.8rem;
          letter-spacing: .2rem;
        }

        @include respond(tab-land){
          font-size: 2.5rem;
        }

        @include respond(phone){
          font-size: 2.5rem;
        }

      }

      p{
        font-size: .2rem;
        font-weight: 250;
        letter-spacing: 1.5px;

        @include respond(big-desktop){
          font-size: 1rem;
          letter-spacing: .1rem;
        }

        @include respond(tab-land){
          font-size: 1.3rem;
          letter-spacing: .1rem;
        }

        @include respond(phone){
          font-size: 1.3rem;
          letter-spacing: .1rem;
        }
      }


    }

    &--humburger{
      position: absolute;
      right: 1rem;
      display: none;
      z-index: 2;

      // @include respond(tab-land){
      //   left: 20rem;
      //   display: block;
      // }

      // @include respond(tab-port){
      //   left: 33rem;
      //   display: block;
      // }



      @include respond(tab-land){
        display: block;
      }

      @include respond(phone){
        display: block;
      }

      .icon{
        font-size: 6rem;
        color:$color-white;

        @include respond(tab-land){
          font-size: 3.8rem;
        }
        @include respond(phone){
          font-size: 3.8rem;
        }
      }

      &:hover{
        cursor: pointer;
      }
    }



    &--ul{
      position: absolute;
      right: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      ul{
        display: flex;
        list-style: none;
        li{
          display: flex;
          justify-content: center;
          align-items: center;

          @include respond(big-desktop){
            padding: 0 1rem;
          }

          @include respond(tab-land){
            display: none;
          }

          @include respond(phone){
            display: none;
          }

          a{
            text-decoration: none;
            padding: 0 .1rem;
            font-weight:300;
            cursor: pointer;
             font-size: $font-p-text-top;
            letter-spacing: 0rem;
            color:$color-white;
            text-shadow: 1px 1px 1px solid black;

            &:hover{
              font-weight: 700;
            }

          }
        }

      }

    }

  }

}

.home{
  padding-top: 3rem;
  width: 100%;
  height: 100vh;
  background-image: url('../assets/herobg.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;

  @include respond(phone){
    height: 50vh;
  }

  &-title{
    position: absolute;
    top:2rem;
    left:2rem;
    width: 60%;
    transform: translate(2rem, 2rem);
    text-align: start;
    padding: .3rem .8rem;

    @include respond(big-desktop){
      top:40%;
      left: 10%;
      padding: 2rem 4rem;
    }

    @include respond(tab-land){
      width: 90%;
      top:10rem;
      left:1rem;
      transform: translate(3rem, 10rem);
      padding: 2rem 2rem;
    }

    @include respond(phone){
      width: 90%;
      top:10rem;
      left:1rem;
      transform: translate(3rem, 10rem);
      padding: 2rem 2rem;
    }

    h1{
      font-size: .5rem;
      font-weight: 500;
      letter-spacing: 2.5px;
      // animation-name: moveToLeft;
      // animation-duration: 5s;
      // animation: moveToLeft 20s  linear infinite;

      @include respond(big-desktop){
        font-size: 3.7rem;
      }

      @include respond(tab-land){
        font-size: 2.5rem;
      }

      @include respond(phone){
        font-size: 2.5rem;
      }

      span{
        color:$color-secundary;
      }
    }

    @keyframes moveToLeft {
      0%{
          opacity: 0;
          transform: translateX(100px);
      }

      50%{

      }

      100%{
        opacity:1;
        transform: translateX(-4px);
      }
    }

    @keyframes display {
      0%{
        opacity: 0;
      }

      20%{
        opacity: .1;
      }

      40%{
        opacity: .2;
      }

      60%{
        opacity: .4;
      }

      80%{
        opacity: .6;
      }

      100%{
        opacity: 1;
      }
    }

    p{
      margin-top: .1rem;
      font-size:$font-p-text-top;
      font-weight: 300;
      letter-spacing: $space-between-text-p;
      animation-name: display;
      animation-duration: 5s;

      @include respond(big-desktop){
        margin: 1rem 0;
        font-size: 1.6rem;
        line-height: 1.2rem;
      }

      @include respond(tab-port){
        font-size: 1.5rem;
      }

      @include respond(phone){
        font-size: 1.5rem;
      }

      span{
        color:$color-secundary;
        font-weight: 600;
      }
    }

    button{
      border: none;
      background-color: transparent;
      transition: all .3s;
      font-weight: 100;

      @include respond(big-desktop){
       margin-top: 3rem;
      }

      @include respond(tab-land){
        margin-top: 3rem;
      }

      @include respond(phone){
        margin-top: 3rem;
      }

      a{
        text-decoration: none;
        padding: .19rem .35rem;
        background-color: $color-secundary2;
        color:$color-white;
        border: none;
        border-radius: .1rem;
        font-size: .2rem;
        font-weight: 100;
        letter-spacing: 1.4px;

        @include respond(big-desktop){
          border-radius: .5rem;
          padding: 1rem 2rem;
          font-size: 1rem;
          font-weight: 100;
        }

        @include respond(tab-land){
          font-size: 1.2rem;
          padding: 1rem  1.5rem;
          border-radius: 1rem;
        }

        @include respond(phone){
          font-size: 1.2rem;
          padding: 1rem  1.5rem;
          border-radius: 1rem;
        }

      }
      &:hover{
        transform: scale(1.1);
        cursor: pointer;
      }
    }

    &--cicle{
      position: absolute;
      width: $bol;
      height: $bol;
      left: 4px;
      top: 0;
      background: $color-secundary;
      border-radius: 100%;

      @include respond(big-desktop){
        width: 2rem;
        height: 2rem;
        left: 0;
      }

      @include respond(tab-land){
        width: 2rem;
        height: 2rem;
        left: 0;
      }

      @include respond(phone){
        width: $bol-phone;
        height: $bol-phone;
        left: -1.7rem;
        top:0;
      }
    }
    &--solid{
      position:absolute;
      width: 0px;
      height: 2.9rem;
      left: .2rem;
      top: 0px;
      border: 1px solid $color-secundary;
      // transform: rotate(90deg);

      @include respond(big-desktop){
        height: 16rem;
        left: .9rem;
      }

      @include respond(tab-land){
        height: 16rem;
        left: .9rem;
      }

      @include respond(phone){
        height: 17rem;
        left: -1rem;
        top:0rem;
      }
    }
  }
}
.section-service--panel{
  background-color: $color-secundary2;
  width: 80%;
  height: auto;
  margin: 0 auto;
  position: fixed;
  top: 1rem;
  left: 6%;
  transform: translate(6%,1rem);
  z-index: 5000;
  border-radius:.2rem ;
  padding:.6rem 1rem;
  transition: all .10s;

  @include respond(big-desktop){
    top: 30%;
    height: 50vh;
    padding: 5rem 6rem;
    border-radius: 1rem ;
  }


  @include respond(tab-port){
    top: 15%;
    left: 8%;
    height: auto;
    transform: transform(50%,50%);
    padding: 5rem 6rem;
    border-radius: 1rem ;
  }

  @include respond(phone){
    top:20%;
    height:auto;
  }

  &-close{
    position: absolute;
    right: .3rem;
    top: 0rem;

    @include respond(big-desktop){
      right: 2rem;
      top: 2rem;
    }

    @include respond(tab-land){
      right: 2rem;
      top: 2rem;
    }

    @include respond(phone){
      right: 2rem;
      top: 2rem;
    }

    .icon{
      font-size: .5rem;
      color: rgb(255, 166, 0);
      transition: all .2s;

      @include respond(big-desktop){
        font-size: 3rem;
      }


      @include respond(tab-land){
        font-size: 3rem;
      }


      @include respond(phone){
        font-size: 3rem;
      }

      &:hover{
        cursor: pointer;
        transform: scale(1.2);
      }
    }
  }
&-1{
  height: 100%;
  display: flex;

  @include respond(phone){
    height: auto;
    display: block;
  }

  div{
    width: 50%;
    height: auto;

    @include respond(big-desktop){
      padding: 0rem 5rem;
    }

    @include respond(tab-land){
      padding: 0rem 0rem;
    }

    @include respond(phone){
      width: 100%;
      height: auto;
      padding: 3rem 0;
    }
  }
    &-img{
      display: block;
      align-items: center;
      position: relative;
      transition: all .2s;
      height: auto;

      @include respond(phone){
        width: 100%;
        height: 100vh;
      }

      .img-1{
        position: absolute;
        top: 0rem;
        left: 0;
        width: 80%;
        height: 80%;
        transition: all .10s;
        display: flex;
        justify-content: center;

        @include respond(tab-land){
          width: 70%;
          height: 70%;
          top: 0rem;
          left: 1.5rem;
        }

      @include respond(phone){
        width: 100%;
        height: 100%;
        display: block;
        top: -4rem;
      }

        &:hover{
          z-index: 2;
          transform: scale(1.1);
          cursor: pointer;
        }
      }

      .img-2{
        position: absolute;
        bottom: 0rem;
        right: 0;
        width: 80%;
        height: 80%;
        transition: all .10s;
        display: flex;
        justify-content: center;

        @include respond(tab-land){
          width: 70%;
          height: 70%;
          // display: block;
          bottom: 0rem;
        }

        @include respond(phone){
          display: none;
        }

        &:hover{
           z-index: 2;
          transform: scale(1.1);
          cursor: pointer;
        }

      }

      img{
        width: 100%;
        border-radius: .1rem;
        border: 2px solid $color-white;
        background-size: cover;
        object-fit: cover;

        @include respond(big-desktop){
          border-radius: 1rem;
        }

        @include respond(tab-land){
          border-radius: 1rem;
        }

        @include respond(phone){
          border-radius: 1rem;
        }
      }
  }

  &-2{
    padding: 0 .2rem;

    h1{
      font-size: $font-h1-text-card;
      // color: rgb(53, 53, 53);
      color:$color-white;
      letter-spacing:$space-between-text-card;
      font-weight: 500;
      text-shadow: 1px 1px 1px 1px black;
      margin-bottom: .3rem;

      @include respond(big-desktop){
        font-size: 1.9rem;
        margin-bottom: 2.2rem;
        font-weight: 400;
      }

      @include  respond(tab-land){
        font-size:1.5rem ;
      }

      @include respond(phone){
          font-size: 1.5rem;
          text-align: center;
          margin-bottom: 2.5rem;
          font-weight: 500;
          letter-spacing: 0.5rem;
      }
    }

    p{
      text-align: justify;
      font-size: $font-p-text-card;
      color:$color-white;
      font-weight: 300;
      letter-spacing: 0.1px;
      text-shadow: 1px 1px 1px 1px black;
      line-height: .33rem;

      @include respond(big-desktop){
          line-height: 1.7rem;
      }

      @include  respond(tab-land){
        line-height: 1.7rem;
        font-size: 1.2rem;
        letter-spacing: 0.1px;
      }

      @include respond(phone){
        line-height: 1.7rem;
        font-size: 1.2rem;
        letter-spacing: 0.1px;
      }

    }

    .secure_panel{
       display:flex;
       width: 100%;
       height: auto;

      @include respond(big-desktop){
        padding: 2rem 0;
      }

      @include respond(tab-port){
        padding: 1rem 0;
      }

      @include respond(phone){
        padding:0rem 0 ;
      }

      div{
        height: auto;
        text-align: center;
        margin-bottom: .2rem;
        h4{
          // color:rgb(53, 53, 53);
          color:$color-white;
          font-size: .2rem;
          font-weight: 500;
          letter-spacing: 2px;

          @include respond(big-desktop){
            font-size: 1.5rem;
            font-weight: 300;
            margin-bottom: 0rem;
          }

          @include respond(tab-port){
            font-size: 1rem;
          }

          @include respond(phone){
            font-size: 1rem;
          }
        }
      }

      .iconSafe{
        font-size: .5rem;
        color:$color-white;

        @include respond(big-desktop){
          font-size: 2.7rem;
        }

        @include respond(tab-land){
          font-size: 2.5rem;
        }

        @include respond(phone){
          font-size: 2.5rem;
        }
      }

    }
    .last-p{
      text-align: center;
      font-size: .2rem;
      letter-spacing: 3px;
      margin-bottom: .4rem;

      @include respond(big-desktop){
        font-size: 1.1rem;
        font-weight: 200;
      }

      @include respond(tab-land){
        font-size: .9rem;
        //margin-top: -2rem;
      }

      @include respond(phone){
        font-size: .9rem;
        margin-top: -2rem;
      }
    }

    .btn-whatsapp{
      border: none;
      border-radius: .1rem;
      transition: all .2s;
      margin:0px auto;
      display: flex;
      align-items: center;

      @include respond(big-desktop){
        border-radius: .5rem;
      }

      @include respond(tab-land){
        border-radius: .5rem;
      }

      @include respond(phone){
        border-radius: .5rem;
      }

      a{
        text-decoration: none;
        display: flex;
        align-items: center;
        color:$color-primary;
        font-weight: 500;
        padding: .1rem .3rem;

        @include respond(big-desktop){
          padding: .5rem 2rem;
        }

        @include respond(tab-land){
          padding: .7rem 2rem;
          font-size: 1.4rem;
        }

        @include respond(phone){
          padding: .7rem 2rem;
          font-size: 1.4rem;
        }
      }

      .icon{
        font-size: .3rem;
        margin-left: .1rem;
        color: $color-button-whatsapp;

        @include respond(big-desktop){
          font-size: 1.5rem;
          margin-left: .6rem;

        }

        @include respond(tab-land){
          font-size: 1.5rem;
          margin-left: .6rem;
        }

        @include respond(phone){
          font-size: 1.5rem;
          margin-left: .6rem;
        }
      }

      &:hover{
        transform: scale(1.1);
        cursor: pointer;
      }
    }
  }
}
}

