.services{
  padding: 1rem 1rem;
  position: relative;
  height: auto;

  @include respond(big-desktop){
    padding: 8rem 6rem;
    // text-align: center;
  }

  @include respond(tab-land){
    padding: 5rem 6rem;
  }

  @include respond(phone){
    padding: 5rem 1rem;
  }

  .styles-right{
    &--cicle{
      position: absolute;
      width: $bol;
      height: $bol;
      right: 1rem;
      top: 0.3rem;
      background: $color-secundary;
      border-radius: 100%;

      @include respond(big-desktop){
        width: 2rem;
        height: 2rem;
        right: 5rem;
        top: 5rem;
      }

      @include respond(tab-land){
        width: 2rem;
        height: 2rem;
        right: 5rem;
        top: 5rem;
      }

      @include respond(phone){
        right: .7rem;
        top: 2.5rem;
        width: $bol-phone;
        height: $bol-phone;
      }
    }
    &--solid{
      position:absolute;
      width: 0px;
      height: 4rem;
      right: 70px;
      top: 0.6rem;
      border: 1px dashed  $color-secundary;

      @include respond(big-desktop){
        height: 30vh;
        right: 5.9rem;
        top: 7rem;
      }

      @include respond(tab-land){
        height: 30vh;
        right: 5.9rem;
        top: 7rem;
      }

      @include respond(phone){
       height: 18rem;
       right: 1.5rem;
       top: 4.5rem;
      }
    }

  }

  h1{
    letter-spacing:.1rem ;
    font-weight: 700;
    font-size: $font-h1;
    text-shadow: 1px 1px 1px 1px black;
    color: $color-secundary;

    @include respond(big-desktop){
      letter-spacing: .3rem;
      padding: 0rem 0;
      font-size: $font-title-big-desktop;
    }

    @include respond(tab-land){
      letter-spacing: .3rem;
      padding: 0rem 0;
      font-size: 2.4rem;
    }

    @include respond(phone){
      font-size: $font-h1_phone-min;

   }
  }

  &-p{
    width:80%;
    padding: .5rem 1rem;
    font-weight: 300;
    letter-spacing:$space-between-text-p;
    font-size: $font-p-text;
    text-align: justify;
    line-height: .4rem;
    color:white;

    @include respond(big-desktop){
      width: 50%;
      line-height: $line-space-p-big-desktop;
      line-height: 2.3rem;
      font-size: $font-p-text-big-desktop;
    }

    @include respond(tab-land){
      width: 80%;
      line-height: $line-space-p-big-desktop;
      font-size:$font-p-text_phone-min ;
    }

    @include respond(phone){
      width:100%;
      font-size: $font-p-text_phone-min;
      line-height:$line-space-p;
      letter-spacing: $lette-space-phone-p;
      padding: 3rem 2rem;
   }
  }

  &_card-part{
    width: 100%;
    height: auto;
    padding: .5rem 1rem;
    display: inline-flex;

    @include respond(big-desktop){
      padding: 0rem 31rem;
      padding-top: 6rem;
    }

    @include respond(tab-land){
      margin-top: 3rem;
      padding: 1rem 0rem;
    }

    @include respond(phone){
      display: block;
      padding: 1rem 0rem;
    }

    span{
      display: block;
    }

    .part-1{
      width: 50%;
      height: 50vh;
      display: flex;
      justify-content: center;

      @include respond(big-desktop){
        height: 35vh;

      }

      @include respond(phone){
        width: 100%;
        height: 25vh;
      }
    }

    .part-2{
      width: 50%;
      height: 50vh;
      display: flex;
      justify-content: center;

      @include respond(big-desktop){
        height: 35vh;
      }

      @include respond(phone){
        width: 100%;
        height: 25vh;
      }
    }

    .card{
      perspective: 150rem;
      -moz-perspective: 150rem;
       position:relative;
       text-align: center;

      &__side{
        height: 3.5rem;
        width: 3.5rem;
        transition: all .8s ease;
        position: absolute;
        top:0;
        left: 0;
        backface-visibility: hidden;
        border: 1px dashed $color-button-secundary;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: .5rem;

        @include respond(big-desktop){
          margin-left: 10rem;
          width: $width-card-big-desktop;
          height: $width-card-big-desktop;
          top:0;
          left: 0;
        }

        @include respond(tab-land){
          margin-left: 2rem;
          width:  18rem;
          height: 18rem;
          top:0;
          left: 0;
        }

        @include respond(phone){
          height: 18rem;
          width: 18rem;
          bottom:0;
          left: 0;
        }

        &--front{

          h2{
            font-size:$font-title-card-service;
            font-weight: $font-weight-h1;
            letter-spacing: $space-between-text-card;
            color:$color-white;
            line-height: $line-space-card;

          @include respond(big-desktop){
              font-size: $font-p-text-card-big-desktop;
              line-height:$line-sapce-h1-card-big-desktop;
          }

          @include respond(tab-land){
            line-height: 2rem;
