        padding: .2rem 0rem;


        @include respond(big-desktop){
          border-radius: 3rem;
        }

        @include respond(tab-land){
          border-radius: 3rem;
        }

        @include respond(phone){
          width: 80%;
          margin: 0 auto;
          border-radius: 1.5rem;
        }

        h4{
          font-size: .6rem;

          @include respond(big-desktop){
            font-size: 5rem;
          }

          @include respond(tab-land){
            font-size: 5rem;
          }

          @include respond(phone){
            font-size: 4rem;

          }

          span{
            font-size: .3rem;
            @include respond(big-desktop){
              font-size: 2rem;
            }

            @include respond(tab-land){
              font-size: 2rem;
            }

            @include respond(phone){
              font-size: 2rem;
            }
          }
        }

        h3{
          font-size: .329rem;
          font-weight: 600;
          margin-bottom: .2rem;
          text-decoration: line-through;

          @include respond(big-desktop){
            font-size: 2rem;
          }

          @include respond(tab-land){
            font-size: 2rem;

          }

          @include respond(phone){
            font-size: 2rem;

          }
        }

