         }

         @include respond(phone){
          margin: 2rem 0;
         }
        li{
          font-size: .255rem;
          list-style: none;
          font-weight: 300;

          @include respond(big-desktop){
            font-size: 1.5rem;
          }

          @include respond(tab-land){
            font-size: 2rem;

          }

          @include respond(phone){
            font-size: 1.3rem;

          }
        }
      }

      button{
        padding: .155rem 1rem;
        border: none;
        border-radius: .3rem;
        font-weight: 400;
        transition: ease 2s;

        &:hover{
          cursor: pointer;
          transform: scale(1.1);
        }

        a{
           color: $color-primary;
           text-decoration: none;
           display: flex ;
           justify-content: center;
           align-items: center;

           .icon{
            margin-left: .1rem;
            color: $color-button-whatsapp;
            font-size: .3rem;

            @include respond(big-desktop){
              font-size: 1.7rem;
              margin-left: .5rem;

            }

            @include respond(tab-land){
              font-size: 1.7rem;
              margin-left: .5rem;

           }

           @include respond(phone){
            font-size: 2rem;
           }
          }
        }

        @include respond(big-desktop){
          padding: 1rem 2rem;
          border-radius: 3rem;
          width: 90%;
        }

        @include respond(phone){
          padding: 1rem 2rem;
          width: 90%;
          border-radius: 3rem;        }
      }
    }

  }
}

.section_campany{
  margin: 1rem 0;
  padding: .4rem 0rem;
  padding-right: 0rem;
  background-color: $color-white;
  width: 50%;
  height: 25vh;
  text-align: center;
  clip-path: polygon(0% 2%, 100% 0, 85% 100%, 0% 100%);

  @include respond(big-desktop){
    margin-bottom: 5rem;
    padding: 2.1rem 0;
    padding-right: 4rem;
    height: 15vh;
    width: 50%;
  }

  @include respond(tab-land){
    width: 50%;
    height: auto;
    margin: 3rem 0;
  }

  @include respond(phone){
    width: 100%;
    height: auto;
    margin: 3rem 0;
  }

  h1{
    color: $gray-text;
    margin-bottom: .2rem;
    font-weight: 300;
    font-size:.3rem;

    @include respond(big-desktop){
      font-size: 2rem;
    }



    @include respond(tab-land){
      margin-top: 1rem;
      text-align: center;
      font-size: 1.5rem;
      // margin-left: 2rem;
    }

    @include respond(phone){
      margin-top: 1rem;
      text-align: center;
      font-size: 1.5rem;
      // margin-left: 2rem;
    }
  }

  &-panel{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    justify-content:center;

    &--div{
      width: 1.5rem;
      height: 1.5rem;
      display: flex;
      align-items: center;

      @include respond(big-desktop){
        width: 12rem;
        height: 12rem;
      }

      @include respond(tab-land){
        width: 6rem;
        height: 6rem;
      }

      @include respond(phone){
        width: 6rem;
        height: 6rem;
      }

       &:last-child{
        margin-right: .1rem ;
        width: 1.2rem;
        height: 1.2rem;

        @include respond(big-desktop){
          width: 9rem;
          height: 9rem;
        }

        @include respond(tab-land){
          width: 4.5rem;
          height:4.5rem;
        }
        @include respond(phone){
          width: 4.5rem;
          height:4.5rem;
        }

        img{
          width: 100%;
        }
       }

       img{
        width: 100%;
        transition: all .2s;
        opacity: 0.9;
        &:hover{
          transform: scale(1.1);
          filter:none;
        }
        filter: grayscale(100%);
        -webkit-filter: grayscale(100%)
      }

    }
  }
}

