.whatsapp{
  a{
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    border-radius: 100%;
    display: flex;
    bottom: .2rem;
    right: .2rem;
    position: fixed;
    transition: all .5s;
    background-color: $color-white;
    color: $color-button-whatsapp;

   @include respond(big-desktop){
    bottom: 2rem;
    right: 2rem;
   }
   @include respond(tab-land){
    right: 1rem;
    bottom: 1rem;
  }
    @include respond(phone){
      right: 1rem;
      bottom: 1rem;
    }

    &:hover{
      transform: scale(1.2);
    }

    .icon{
      font-size: .7rem;

      &:hover{
        cursor: pointer;
      }

      @include respond(big-desktop){
        font-size: 4rem;
      }

      @include respond(tab-land){
        font-size: 5rem;
      }

      @include respond(phone){
        font-size: 5rem;
      }
    }
  }
}

